import React, { createContext, useState, useEffect } from "react";
import { useContext } from "react";
import { UrlContext } from "./UrlContext";
import { TokenContext } from "./TokenContext";
import axios from "axios";
import { toast } from "react-toastify";

export const ApiCall = createContext();

export const ApiCallProvider = ({ children }) => {
  const { url } = useContext(UrlContext);
  const { token, userID } = useContext(TokenContext);

  const [userData, setUserData] = useState([]);
  const [postData, setPostData] = useState([]);
  const [eventData, setEventData] = useState([]);

  const config = {
    "Content-Type": "application/json",
    headers: {
      Authorization: `Bearer ${token}`,
    },
  };

  // Users
  /////////////////////////////////////////////////////////////////

  const getUser = async () => {
    try {
      const response = await axios.get(`${url}/users`, config);
      setUserData(response?.data);
      return response?.data?.data?.Users;
    } catch (err) {
      if (err.message === "Network Error") {
        toast.error(`Poor network❕`, {
          position: "top-right",
          autoClose: 1500,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: false,
          draggable: true,
          progress: undefined,
          theme: "colored",
        });
      }
      console.error("Error fetching User:", err);
      throw err;
    }
  };

  const createUser = async reqBody => {
    try {
      await axios.post(`${url}/users`, reqBody, config);
      toast("User Created Successfully 👍", {
        position: "top-right",
        autoClose: 1500,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
        theme: "light",
      });
    } catch (err) {
      toast.error("Error creating User", {
        position: "top-right",
        autoClose: 1500,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: false,
        draggable: true,
        progress: undefined,
        theme: "colored",
      });
      console.error("Error creating User:", err);
      throw err;
    }
  };

  const readUser = async id => {
    try {
      const response = await axios.get(`${url}/users/${id}`, config);
      return response.data;
    } catch (err) {
      if (err.message === "Network Error") {
        toast.error(`Poor network❕`, {
          position: "top-right",
          autoClose: 1500,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: false,
          draggable: true,
          progress: undefined,
          theme: "colored",
        });
      }
      console.error("Error fetching User:", err);
      throw err;
    }
  };

  const updateUser = async (id, reqBody) => {
    try {
      const response = await axios.put(`${url}/users/${id}`, reqBody, config);
      toast("User Updated Successfully 👍", {
        position: "top-right",
        autoClose: 1500,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
        theme: "light",
      });
      console.log("User Updated:", response.data);
    } catch (err) {
      const errorType =
        err.message === "Network Error" ? "Poor network" : "Permission denied";
      toast.error(`${errorType} ❕`, {
        position: "top-right",
        autoClose: 1500,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: false,
        draggable: true,
        progress: undefined,
        theme: "colored",
      });
      console.error("Error updating User:", err);
      throw err;
    }
  };

  const deleteUser = async id => {
    try {
      const response = await axios.delete(`${url}/users/${id}`, config);
      toast("User Deleted Successfully ❗️", {
        position: "top-right",
        autoClose: 1500,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
        theme: "light",
      });
      console.log("User Deleted:", response.data);
    } catch (err) {
      const errorType =
        err.message === "Network Error" ? "Poor network" : "Permission denied";
      toast.error(`${errorType} ❕`, {
        position: "top-right",
        autoClose: 1500,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: false,
        draggable: true,
        progress: undefined,
        theme: "colored",
      });
      console.error("Error deleting User:", err);
      throw err;
    }
  };

  /////////////////////////////////////////////////////////////////

  // Posts
  /////////////////////////////////////////////////////////////////

  const getPost = async () => {
    try {
      const response = await axios.get(`${url}/posts`, config);
      setPostData(response?.data);
      return response?.data;
    } catch (err) {
      if (err.message === "Network Error") {
        toast.error(`Poor network❕`, {
          position: "top-right",
          autoClose: 1500,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: false,
          draggable: true,
          progress: undefined,
          theme: "colored",
        });
      }
      console.error("Error fetching User:", err);
      throw err;
    }
  };

  const createPost = async reqBody => {
    try {
      await axios.post(`${url}/posts`, reqBody, config);
      toast("User Created Successfully 👍", {
        position: "top-right",
        autoClose: 1500,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
        theme: "light",
      });
    } catch (err) {
      toast.error("Error creating Post", {
        position: "top-right",
        autoClose: 1500,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: false,
        draggable: true,
        progress: undefined,
        theme: "colored",
      });
      console.error("Error creating Post:", err);
      throw err;
    }
  };

  const readPost = async id => {
    try {
      const response = await axios.get(`${url}/posts/${id}`, config);
      return response.data;
    } catch (err) {
      if (err.message === "Network Error") {
        toast.error(`Poor network❕`, {
          position: "top-right",
          autoClose: 1500,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: false,
          draggable: true,
          progress: undefined,
          theme: "colored",
        });
      }
      console.error("Error fetching Post:", err);
      throw err;
    }
  };

  const updatePost = async (id, reqBody) => {
    try {
      await axios.put(`${url}/posts/${id}`, reqBody, config);
      // console.log(reqBody);
      toast("Post Updated Successfully 👍", {
        position: "top-right",
        autoClose: 1500,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
        theme: "light",
      });
      // console.log("User Updated:", response.data);
    } catch (err) {
      const errorType =
        err.message === "Network Error"
          ? "Poor network"
          : "Error Updating Post";
      toast.error(`${errorType} ❕`, {
        position: "top-right",
        autoClose: 1500,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: false,
        draggable: true,
        progress: undefined,
        theme: "colored",
      });
      console.error("Error updating Post:", err);
      throw err;
    }
  };

  const deletePost = async (id, img) => {
    try {
      await axios.delete(`${url}/posts/${id}/${img}`, config);
      toast("Post Deleted Successfully ❗️", {
        position: "top-right",
        autoClose: 1500,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
        theme: "light",
      });
      // console.log(" Deleted:", response.data);
    } catch (err) {
      const errorType =
        err.message === "Network Error"
          ? "Poor network"
          : "Error deleting Post";
      toast.error(`${errorType} ❕`, {
        position: "top-right",
        autoClose: 1500,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: false,
        draggable: true,
        progress: undefined,
        theme: "colored",
      });
      console.error("Error deleting Post:", err);
      throw err;
    }
  };

  /////////////////////////////////////////////////////////////////

  // Events
  /////////////////////////////////////////////////////////////////

  const getEvent = async () => {
    try {
      const response = await axios.get(`${url}/events`, config);
      setEventData(response?.data);
      return response?.data;
    } catch (err) {
      if (err.message === "Network Error") {
        toast.error(`Poor network❕`, {
          position: "top-right",
          autoClose: 1500,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: false,
          draggable: true,
          progress: undefined,
          theme: "colored",
        });
      }
      console.error("Error fetching User:", err);
      throw err;
    }
  };

  const createEvent = async reqBody => {
    try {
      await axios.post(`${url}/events`, reqBody, config);
      toast("Event Created Successfully 👍", {
        position: "top-right",
        autoClose: 1500,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
        theme: "light",
      });
    } catch (err) {
      toast.error("Error creating Event", {
        position: "top-right",
        autoClose: 1500,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: false,
        draggable: true,
        progress: undefined,
        theme: "colored",
      });
      console.error("Error creating Event:", err);
      throw err;
    }
  };

  const readEvent = async id => {
    try {
      const response = await axios.get(`${url}/events/${id}`, config);
      return response.data;
    } catch (err) {
      if (err.message === "Network Error") {
        toast.error(`Poor network❕`, {
          position: "top-right",
          autoClose: 1500,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: false,
          draggable: true,
          progress: undefined,
          theme: "colored",
        });
      }
      console.error("Error fetching Event:", err);
      throw err;
    }
  };

  const updateEvent = async (id, reqBody) => {
    try {
      await axios.put(`${url}/events/${id}`, reqBody, config);
      toast("Event Updated Successfully 👍", {
        position: "top-right",
        autoClose: 1500,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
        theme: "light",
      });
      // console.log("User Updated:", response.data);
    } catch (err) {
      const errorType =
        err.message === "Network Error"
          ? "Poor network"
          : "Error Updating Event";
      toast.error(`${errorType} ❕`, {
        position: "top-right",
        autoClose: 1500,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: false,
        draggable: true,
        progress: undefined,
        theme: "colored",
      });
      console.error("Error updating Event:", err);
      throw err;
    }
  };

  const deleteEvent = async id => {
    try {
      await axios.delete(`${url}/events/${id}`, config);
      toast("Event Deleted Successfully ❗️", {
        position: "top-right",
        autoClose: 1500,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
        theme: "light",
      });
      // console.log(" Deleted:", response.data);
    } catch (err) {
      const errorType =
        err.message === "Network Error"
          ? "Poor network"
          : "Error deleting Event";
      toast.error(`${errorType} ❕`, {
        position: "top-right",
        autoClose: 1500,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: false,
        draggable: true,
        progress: undefined,
        theme: "colored",
      });
      console.error("Error deleting Event:", err);
      throw err;
    }
  };

  useEffect(() => {
    const load = async () => {
      if (window.location !== "/login") {
        await getPost();
        await getUser();
        await getEvent();
      }
    };

    load();
  }, []);

  /////////////////////////////////////////////////////////////////

  const contextValue = {
    getUser,
    createUser,
    readUser,
    updateUser,
    deleteUser,
    //
    getPost,
    createPost,
    readPost,
    updatePost,
    deletePost,
    //
    getEvent,
    createEvent,
    readEvent,
    updateEvent,
    deleteEvent,
    //
    userData,
    postData,
    eventData,
  };

  return <ApiCall.Provider value={contextValue}>{children}</ApiCall.Provider>;
};
