import React, { useContext, useEffect } from "react";
import { Breadcrumbs } from "@material-tailwind/react";
import { Link, useNavigate } from "react-router-dom";
import { ApiCall } from "../../components/Funcs/ApiCalls";

const AllEvents = () => {
  const navigate = useNavigate();
  const { eventData } = useContext(ApiCall);

  useEffect(() => {
    console.log(eventData);
  }, []);

  // Function to format a date string
  const formatDate = dateString => {
    const options = { year: "numeric", month: "long", day: "numeric" };
    const date = new Date(dateString);
    return date.toLocaleDateString(undefined, options);
  };

  return (
    <div>
      <div className='p-4 h-14 w-full bg-white flex justify-between items-center'>
        <h1 className='font-semibold'>ALL EVENTS</h1>

        <Link
          to={`/add-event`}
          className='px-6 py-2 hover:bg-gray-500 bg-gray-400 text-white duration-500 transition ease-in-out'
        >
          ADD EVENT
        </Link>
      </div>

      <div className='w-full my-8 '>
        <div className='text-xs md:text-md font-bold bg-gray-300 px-4 py-4 my-2 flex justify-between items-center '>
          <h1 className='w-1/2 text-center'>TITLE</h1>
          <h1 className='w-1/2 text-center'>Date</h1>
        </div>

        {eventData?.map((data, index) => (
          <div
            onClick={() => navigate(`/edit-event/${data._id}`)}
            className='cursor-pointer hover:bg-gray-400 hover:text-white duration-500 transition ease-in-out bg-white my-4 px-4 py-4 flex justify-between items-center '
          >
            <h1 className='w-1/2 text-center border-r'>
              {data.title.toUpperCase()}
            </h1>
            <h1 className='w-1/2 text-center'>{formatDate(data.date)}</h1>
          </div>
        ))}
      </div>
    </div>
  );
};

export default AllEvents;
